import { imagesConstants } from '../_constants';
export function images(state = {loading: false, uploading: false, images: []}, action) {
  switch (action.type) {
     case imagesConstants.ADD_REQUEST:
       return { ...state, uploading: true,  
                };
     case imagesConstants.ADD_SUCCESS:
       
       return { ...state,
                uploading: false,
                images: [ ...state.images,...action.image, ]
                 };
    case imagesConstants.ADD_FAILURE:
      return { 
        ...state,
        uploading: false,
        error: action.error };
    case imagesConstants.GETALL_REQUEST:
      return {
        ...state,
        loading: true
      };
    case imagesConstants.GETALL_SUCCESS:
      return {
        ...state,
        images: [],
        loading: false
      };
    case imagesConstants.GETALL_FAILURE:
      return { 
        error: action.error,
        loading: false
      };
    case imagesConstants.DELETE_REQUEST:
      // add 'deleting:true' property to image being deleted
      return {
        ...state,
        images: state.images.map(image =>
          image === action.id
            ? { image, deleting: true }
            : image
        )
      };
    case imagesConstants.DELETE_SUCCESS:
      // remove deleted image from state
     

      return {
        images: state.images.filter(image => image.image !== action.id)
      };
    case imagesConstants.DELETE_FAILURE:
      // remove 'deleting:true' property and add 'deleteError:[error]' property to image 
      return {
        ...state,
        images: state.images.map(image => {
          if (image.image === action.id) {
            // make copy of image without 'deleting:true' property
            const { deleting, imageCopy } = image;
            // return copy of image with 'deleteError:[error]' property
            return { imageCopy, deleteError: action.error };
          }

          return image;
        })
      };
    default:
      return state
  }
}