import React from 'react';

import { connect } from 'react-redux';
import {  testimonialActions } from '../_actions';

//import { Button, Col, Row, Container } from 'react-bootstrap';

class TestimonialEdit extends React.Component {
	constructor(props) {
		super(props);
		this.state = { testimonial : '',
						delete: false };
		
		
	}
	
	componentDidMount() {
		this.props.dispatch(testimonialActions.getAll());
	}
	
	handleDeleteQuote(id) {
	   
		
		return (e) => this.props.dispatch(testimonialActions.delete(id));
	}
   

	render() {
		const {  testimonial } = this.props;

		return (
			
				<div className="container" >
					
					<h2>{testimonial.loading && <em>Loading listings....</em>}</h2>
					<h3 className="text-center">{testimonial.error && <span className="text-danger">ERROR: {testimonial.error} </span>}</h3>
					{testimonial.items && <div>
						{ testimonial.items.map((testimony, index) => 
							<div key={testimony.id} className="row mx-1 my-1 px-1 py-1 bg-dark" >
								<div className= "col bg-secondary">
									<div className="row ">
										<div className="col align-self-center bg-primary">
										<blockquote className="blockquote">
  											<p className="mb-0 text-center">{testimony.quote}</p>
  											<footer className="blockquote-footer text-center text-dark"><cite title={testimony.person}>{testimony.person}</cite></footer>
										</blockquote>                             
											
										</div>
									</div>
									<div className="row bg-light">
										<div className="col align-self-center text-left text-primary">
											{ testimony.deleting ? <em> - Editing...</em> 
												: testimony.deleteError ? <span className="text-danger"> - ERROR: {testimony.deleteError}</span> 
												: <small> -  <a href="/">Edit</a></small> 
											}
										</div>
										<div className="col align-self-center text-right text-primary">
											{ testimony.deleting ? <em> - Deleting...</em> 
												: testimony.deleteError ? <span className="text-danger"> - ERROR: {testimony.deleteError}</span> 
												: <small> -  <a href="#" onClick={this.handleDeleteQuote(testimony.id)}>Delete</a></small> 
											}
										</div>
									</div>
								</div>
							</div>
						)}
					</div> }
				</div>
			
		);
	}
}

function mapStateToProps(state) {

const { testimonial } = state;

	return {
		
		testimonial
		

	};
}

const connectedTestimonialEdit = connect(mapStateToProps)(TestimonialEdit);
export { connectedTestimonialEdit as TestimonialEdit };




