import React from 'react';
import {Nav  } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { routesConstants } from '../_constants';
import '../bootstrap-social-gh-pages/bootstrap-social.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faStroopwafel } from '@fortawesome/free-solid-svg-icons';
import { faTwitter, faFacebook, faLinkedin, faInstagram, faSnapchat } from '@fortawesome/free-brands-svg-icons';


library.add(faStroopwafel, faTwitter, faFacebook, faLinkedin, faInstagram, faSnapchat);
class NavPage extends React.Component {
  
	
	render() {
	   
		
		const key = this.props.location.pathname;
		
		
		return (
			<div className="bg-primary sticky-top">
				<div className="row justify-content-right">
					<div className="col text-right">
						<a className="btn btn-social-icon btn-primary" href="http://facebook.com"><span className="text-center"><FontAwesomeIcon icon={["fab", "facebook"]}/></span></a>
  						<a className="btn btn-social-icon btn-primary" href="http://linkedin.com"><span><FontAwesomeIcon icon={["fab", "linkedin"]}/></span></a>
						<a className="btn btn-social-icon btn-primary" href="http://twitter.com"><span><FontAwesomeIcon icon={["fab", "twitter"]}/></span></a>
  						<a className="btn btn-social-icon btn-primary" href="http://instagram.com"><span><FontAwesomeIcon icon={["fab", "instagram"]}/></span></a>
  						<a className="btn btn-social-icon btn-primary" href="http://snapchat.com"><span><FontAwesomeIcon icon={["fab", "snapchat"]}/></span></a>
  					</div>
				</div>
				<div className="row justify-content-center">
					
					<Link to={routesConstants.HOME} className="text-dark text-center" style={{ textDecoration: 'none' }}>
					
					<h1>Your Company Logo here </h1>
					<h6> fancy slogan</h6>
					</Link>
				</div>
						<Nav variant="tabs" activeKey={this.props.location.pathname}  className="justify-content-center nav-fill">
							<Nav.Item className="btn-primary">
								<Nav.Link className="text-dark" disabled={ (key === routesConstants.HOME) ? true : false} eventKey={routesConstants.HOME}  href={routesConstants.HOME}> Home </Nav.Link>
							</Nav.Item>
							<Nav.Item className="btn-primary">
								<Nav.Link className="text-dark" disabled={ (key === routesConstants.LISTING) ? true : false} eventKey={routesConstants.LISTING}  href={routesConstants.LISTING}> Listing </Nav.Link>
							</Nav.Item>
							<Nav.Item  className="btn-primary">
								<Nav.Link className="text-dark" disabled={ (key === routesConstants.SELL) ? true : false} eventKey={routesConstants.SELL}  href={routesConstants.SELL}>Sell</Nav.Link>
							</Nav.Item>
							<Nav.Item className="btn-primary">
								<Nav.Link className="text-dark" disabled={ (key === routesConstants.REQUEST) ? true : false} eventKey={routesConstants.REQUEST}  href={routesConstants.REQUEST}>Request</Nav.Link>
							</Nav.Item>
							<Nav.Item className="btn-primary">
								<Nav.Link className="text-dark" disabled={ (key === routesConstants.TESTIMONIAL) ? true : false} eventKey={routesConstants.TESTIMONIAL}  href={routesConstants.TESTIMONIAL}>Testimonials</Nav.Link>
							</Nav.Item>
						</Nav>

			</div>
			);
	}
}

function mapStateToProps(state) {
	const { authentication } = state;
	const { loggedIn } = authentication;
	return {
		loggedIn
	};
}

const connectedNavPage = withRouter(connect(mapStateToProps)(NavPage));
export { connectedNavPage as NavPage };
export default withRouter(connect(mapStateToProps)(NavPage))

