
import React from 'react';

import { connect } from 'react-redux';

import {  listingActions } from '../_actions';

//import { Card,  CardText, CardBody, CardTitle, CardHeader, CardFooter, Button, Col, Row, Container } from 'react-bootstrap';


class ListingEdit extends React.Component {
    constructor(props) {
        super(props);
        this.state = { listing : '' };
        this.clickOnListing = this.clickOnListing.bind(this);
    }
    componentDidMount() {
        this.props.dispatch(listingActions.getAll());
    }
    clickOnListing(event, listing){
        const {title } = listing;

        console.log("Clicked on listing " + title );
    }
    handleDelete(id) {
        return (e) => this.props.dispatch(listingActions.delete(id));
    }
    render() {
        const { listings } = this.props;
        
       
        
        return (
            <div className="container" >
                
                {listings.loading && <em>Loading listings....</em>}
                {listings.error && <span className="text-danger">ERROR: {listings.error} </span>}
                {listings.items && 
                     <div >
                    { listings.items.map((listing, index) => 
                        <div key={index} className="row px-1 py-1 mx-1 my-1 bg-dark" >
                            <div className="col bg-success">
                            <div onClick={ (event) => {this.clickOnListing(event, listing) } }>
                            <div className="row  container">
                                 {listing.images.map((image, index) =>  
                                <div className="col mx-1 px-1 my-1 py-1" key={index} > 
                                    <ListingImage image={image} />
                                </div>
                                )}
                            </div>
                            <div className="row justify-content-center">
                                <div className="col text-left">
                                    <p>{listing.address }</p>
                                </div>
                            </div>
                            <div className="row align-items-center">
                                <div className="col text-left">                             
                                    <span className="align-items-center">{listing.city}</span>
                                </div>
                            </div>
                            </div>
                            <div className="row bg-secondary">
                                <div className="col text-left">
                                    <span>Edit</span>
                                </div> 
                                <div className="col text-right">
                                { listing.deleting ? <em> - Deleting...</em> 
                                    : listing.deleteError ? <span className="text-danger"> - ERROR: {listing.deleteError}</span> 
                                    : <small> -  <a href="#" onClick={this.handleDelete(listing.id)}>Delete</a></small> 
                                }
                                </div>
                            </div>
                        </div>
                    </div>
                    )}
                </div>
                }
                
            </div>
        );
    }
}
class ListingImage extends React.Component {
    
   
    render(){
        const data = this.props.image.data.data;
        var image = '';
        var bytes = [].slice.call(new Uint8Array(data));
        bytes.forEach((b) => image += String.fromCharCode(b));
        const baseName = this.props.image.baseName;
        
        
        var imgCss = {
            'maxWidth' : '140px',
            'maxHeight': '140px',
        };
        
        return  <img src={image} alt={baseName} style={imgCss}/>;
    }

}
function mapStateToProps(state) {
    const { listings } = state;

    return {
        
        listings
        

    };
}

const connectedListingEdit = connect(mapStateToProps)(ListingEdit);
export { connectedListingEdit as ListingEdit };