import React from 'react';
import { Router, Route,Switch } from 'react-router-dom';
import { connect } from 'react-redux';
import { history } from '../_helpers';
import { alertActions } from '../_actions';
import { PrivateRoute } from '../_components';
import { routesConstants } from '../_constants';
import { HomePage } from '../HomePage';
import { LoginPage } from '../LoginPage';
import { RegisterPage } from '../RegisterPage';
import { ListingPage } from '../Listings';
import { REAPage } from '../REA';
import { RequestInfo, ListHouse } from '../Contact';
import { NavPage } from '../NavPage';
import { TestimonialList } from '../Testimonial';
import Footer from './footer';
import {NotFound} from "./NotFound";

class App extends React.Component {
	constructor(props) {
		super(props);
		const { dispatch } = this.props;
		history.listen((location, action) => {
			// clear alert on location change
			dispatch(alertActions.clear());
		});
	}

	render() {
		 let divPad = {
				paddingTop: '120px',
		};
		let centerDiv = {
			textAlign: 'center',
		};
		const { alert } = this.props;
		return (
			<span>
				<Router history={history}>
				<div className="bg-white">
					<NavPage />
					<div style={{minHeight: "500px"}}>
						{alert.message && 
							<div className="container">
								<div className={`alert ${alert.type}`} style={centerDiv}>{alert.message}</div>
							</div>
						}
						<Switch>
							<Route path={routesConstants.HOME} exact component={HomePage} />
							<Route path={routesConstants.REQUEST} component={ RequestInfo}/>
							<Route path={routesConstants.SELL} component={ListHouse} />
							<Route path={routesConstants.LOGIN} component={LoginPage} />
							<Route path={routesConstants.REGISTER} component={RegisterPage} />
							<Route path={routesConstants.LISTING} component={ListingPage} />
							<Route path={routesConstants.TESTIMONIAL } component={TestimonialList} />
							
							<PrivateRoute path={routesConstants.ADMIN} component = {REAPage}/>
							<Route component={NotFound} />
						</Switch>
					</div>
					<div  style={divPad}>
						<Footer />
					</div>
				</div>
				</Router>
			</span>
			
		);
	}
}

function mapStateToProps(state) {
	const { alert } = state;
	return {
		alert
	};
}

const connectedApp = connect(mapStateToProps)(App);
export { connectedApp as App }; 