import { testimonialConstants } from '../_constants';
let storedTestimony = JSON.parse(localStorage.getItem('testimony'));

const initState = storedTestimony ? { gotList: true, loading: false, submitted: false, items: [...storedTestimony] } : {} ;
export function testimonial(state = initState, action) {
  switch (action.type) {
     case testimonialConstants.ADD_REQUEST:

       return { 
          loading: true, 
          gotList: false, 
         submitted : false };
     case testimonialConstants.ADD_SUCCESS:
       return { 
         
         loading: false,
         gotList: true, 
         items: action.testimonial,
         submitted: true
                 };
    case testimonialConstants.ADD_FAILURE:
      return { ...state,
               submitted: false,
               error: action.error
                };
    
    case testimonialConstants.GETALL_REQUEST:
      console.log(initState);
      return {
        loading: true,
        gotList: false,

      };
    case testimonialConstants.GETALL_SUCCESS:
        

      return {
        items: action.testimony,
        loading: false,
        gotList: true
      };
    case testimonialConstants.GETALL_FAILURE:
      return { 
        error: action.error,
        loading: false,
        gotList: false
      };
    case testimonialConstants.DELETE_REQUEST:
      // add 'deleting:true' property to listing being deleted
      return {
        ...state,
        items: state.items.map(testinomial =>
          testinomial.id === action.id
            ? { ...testinomial, deleting: true }
            : testinomial
        )
      };
    case testimonialConstants.DELETE_SUCCESS:
      // remove deleted listing from state
      return {
        items: state.items.filter(testinomial => testinomial.id !== action.id)
      };
    case testimonialConstants.DELETE_FAILURE:
      // remove 'deleting:true' property and add 'deleteError:[error]' property to listing 
      return {
        ...state,
        items: state.items.map(testinomial => {
          if (testinomial.id === action.id) {
            // make copy of listing without 'deleting:true' property
            const { deleting, ...testinomialCopy } = testinomial;
            // return copy of listing with 'deleteError:[error]' property
            return { ...testinomialCopy, deleteError: action.error };
          }

          return testinomial;
        })
      };
    default:
      return state
  }
}