import { configConst } from '../_constants';
import { authHeader } from '../_helpers';

export const imagesService = {
    create,
    getAll,
    getById,
    update,
    delete: _delete
};


function getAll() {
    const requestOptions = {
        method: 'GET',
        
    };

    return fetch(`${configConst.apiUrl}/uploads`, requestOptions).then(handleResponse);
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        
    };

    return fetch(`${configConst.apiUrl}/uploads/image_data/${id}`, requestOptions).then(handleResponse);
}

function create(file) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader() },
        body: file,
        file: file
        
    };

    return fetch(`${configConst.apiUrl}/uploads/upload`, requestOptions).then(handleResponse);
}

function update(listing) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(listing)
    };

    return fetch(`${configConst.apiUrl}/uploads/update/${listing.id}`, requestOptions).then(handleResponse);;
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/uploads/delete/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                //location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}