import { listingConstants } from '../_constants';
import { listingService } from '../_services';
import { alertActions } from './';
import { history } from '../_helpers';

export const listingActions = {
    getAll,
    getById,
    create,
    update,
    delete: _delete
};


function create(listing) {
    return dispatch => {
        dispatch(request(listing));

        listingService.create(listing)
            .then(
                listing => { 
                    dispatch(success(listing));

                    
                    dispatch(alertActions.success('Adding Listing successful'));
                    
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(listing) { return { type: listingConstants.ADD_REQUEST, listing } }
    function success(items) { return { type: listingConstants.ADD_SUCCESS, items } }
    function failure(error) { return { type: listingConstants.ADD_FAILURE, error } }
}

function getAll() {
    return dispatch => {
        dispatch(request());

        listingService.getAll().then(
                listings => dispatch(success(listings)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: listingConstants.GETALL_REQUEST } }
    function success(listings) { return { type: listingConstants.GETALL_SUCCESS, listings } }
    function failure(error) { return { type: listingConstants.GETALL_FAILURE, error } }
}

function getById(id){
    return dispatch => {
        dispatch(request(id));

        listingService.getById(id).then(
            listing => dispatch(success(listing)),
            error => dispatch(failure(error.toString()))
        );
    };
    function request(id) { return { type: listingConstants.GET_REQUEST } } 
    function success(listing) { return { type: listingConstants.GET_SUCCESS } } 
    function failure(error) { return { type: listingConstants.GET_FAILURE } } 
}
function update(id){
    return dispatch => {
        dispatch(request(id));

        listingService.update(id)
            .then(
                listing => dispatch(success(id)),
                   error => dispatch(failure(id, error.toString()))
                );

    };
    function request(id) { return { type: listingConstants.UPDATE_REQUEST, id } } 
    function success(id) { return { type: listingConstants.UPDATE_SUCCESS, id } } 
    function failure(id, error) { return { type: listingConstants.UPDATE_FAILURE, id, error } } 
}
// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));

        listingService.delete(id)
            .then(
                listing => dispatch(success(id)),
                error => dispatch(failure(id, error.toString()))
            );
    };

    function request(id) { return { type: listingConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: listingConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: listingConstants.DELETE_FAILURE, id, error } }
}