import React from 'react';
import { connect } from 'react-redux';
import LazyHero from 'react-lazy-hero';
import house6 from '../Images/house6.jpg';
const INITIAL_STATE = {


    title: '',
    message: 'Enter a Message...',
    error: null,

    
    };
class RequestInfo extends React.Component {
  constructor(props) {
        super(props);

        this.state = {
            requestInfo: {
                ...INITIAL_STATE
            },
            submitted: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
    }

    handleChange(event) {
        const { name, value } = event.target;
        const { requestInfo } = this.state;
        this.setState({
            requestInfo: {
                ...requestInfo,
                [name]: value
            }
        });
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: false });
        const { requestInfo } = this.state;
        //const { dispatch, user } = this.props;
        //const { _id } = user;
        //requestInfo.id = _id;
        /*if (requestInfo.title && requestInfo.desc ) {
            dispatch(messages.create(requestInfo));
        }*/
        this.setState({ requestInfo: { ...INITIAL_STATE },
                      show: false,
      });
       console.log(this.state);
    }
    handleCancel() {
      this.setState({ requestInfo: { ...INITIAL_STATE },
                      show: false,
      });
      

    }

    render() {
        
        const { requestInfo, submitted } = this.state;

        return (
            <div>
                         <div>   
                        <LazyHero imageSrc={house6} minHeight={'10vh'} isFixed={true}  parallaxOffset={100}>
                 <h2 className="text-center">Request Infomation</h2>
                  </LazyHero>
                  </div>
            <div className="my-2 py-3" >
            <div className="row align-items-center">
            <div className="col-10 offset-1 px-3 py-3">
                
                <form name="form" onSubmit={this.handleSubmit}>
                    <div className={'form-group' + (submitted && !requestInfo.title ? ' has-error' : '')}>
                        
                        <input type="text" className="form-control" name="title" value={requestInfo.title} onChange={this.handleChange} />
                        {submitted && !requestInfo.title&&
                            <div className="help-block"> Title is required</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !requestInfo.desc ? ' has-error' : '')}>
                        
                        <textarea className="form-control"  value={this.state.message} onChange={this.handleChange} rows="10" cols="80"/>
                        {submitted && !requestInfo.desc &&
                            <div className="help-block"> Message is required</div>
                        }
                    </div>

                    <div className="form-group">
                        <button className="btn btn-primary">Requesting Information</button>
                        { submitted &&
                            <img src="data:image/gif;base64," />
                        }
                        <button className="btn btn-link" onClick={this.handleCancel} >Cancel</button>
                    </div>
                </form>
                </div>
            </div>
            </div>
            </div>
        );
    }
    
}
function mapStateToProps(state) {
    return {      
    };
}

const connectedRequestInfo = connect(mapStateToProps)(RequestInfo);
export { connectedRequestInfo as RequestInfo };

export default RequestInfo;