import React from 'react';

import { connect } from 'react-redux';



class ListImage extends React.Component {
    
   
    render(){
        const data = this.props.image.data.data;
        var binary = '';
        var bytes = [].slice.call(new Uint8Array(data));
        bytes.forEach((b) => binary += String.fromCharCode(b));
        const baseName = this.props.image.baseName;
        
        
        var imgCss = {
            'maxWidth' : '140px',
            'maxHeight': '140px',
        };
        
        return  <img src={binary} alt={baseName} style={imgCss}/>;
    }

}
function mapStateToProps(state) {
    
    return {
    };
}

const connectedListingEdit = connect(mapStateToProps)(ListImage);
export { connectedListingEdit as ListImage };