import { configConst } from '../_constants';
import { authHeader } from '../_helpers';

export const testimonialService = {
    create,
    getAll,
    getById,
    update,
    delete: _delete
};


function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/testimonial/index`, requestOptions).then(handleResponse).then(testimony => {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            
            localStorage.setItem('testimony', JSON.stringify(testimony));
            //console.log(testimony);
            return testimony;
        });
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/testimonial/testimonial/${id}`, requestOptions).then(handleResponse);
}

function create(testimonial) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(testimonial)
    };

    return fetch(`${configConst.apiUrl}/testimonial/create`, requestOptions).then(handleResponse);
}

function update(testimonial) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(testimonial)
    };

    return fetch(`${configConst.apiUrl}/testimonial/update/${testimonial.id}`, requestOptions).then(handleResponse);;
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/testimonial/delete/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                //location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}