import React from 'react';


import { connect } from 'react-redux';

import {  listingActions } from '../_actions';
import StackGrid from "react-stack-grid";
import { Card,  Button, Container,  Modal, Row, Col} from 'react-bootstrap';

import LazyHero from 'react-lazy-hero';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faBath, faBed, faCar, faDollarSign, faHome } from '@fortawesome/free-solid-svg-icons';

import house5 from '../Images/house5.jpg';

library.add(faBath, faBed, faCar, faDollarSign, faHome );


const INITIAL_STATE = {


    title: '',
    message: 'Enter a Message...',
    error: null,

    
    };
    
class ListingPage extends React.Component {
    constructor(props) {
        super(props);
         this.state = {
             requestInfo: {...INITIAL_STATE },
             submitted: false,
             listing : '',
             show: false 

        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
        this.clickOnListing = this.clickOnListing.bind(this);
        this.closeView = this.closeView.bind(this)
        
    }
    componentDidMount() {
        this.props.dispatch(listingActions.getAll());
    }
    clickOnListing(event, listing){
        const {address } = listing;
         this.setState({show: true,
                        address: address});
        console.log("Clicked on listing " + address );
        console.log(this.state.show);
    }
    closeView(){
        this.setState({show: false});
        console.log(this.state.show)
    }
    handleChange(event) {
        const { name, value } = event.target;
        const { requestInfo } = this.state;
        this.setState({
            requestInfo: {
                ...requestInfo,
                [name]: value
            }
        });
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: false });
        
        //const { dispatch, user } = this.props;
        //const { _id } = user;
        //requestInfo.id = _id;
        /*if (requestInfo.title && requestInfo.desc ) {
            dispatch(messages.create(requestInfo));
        }*/
        this.setState({ requestInfo: { ...INITIAL_STATE },
                      show: false,
      });
       console.log(this.state);
    }
    handleCancel() {
      this.setState({ requestInfo: { ...INITIAL_STATE },
                      show: false,
      });
      

    }
    

    render() {
        const { listings } = this.props;
        
      
        return (
            <div>
<div>   
                        <LazyHero imageSrc={house5} minHeight={'10vh'} isFixed={true}  parallaxOffset={100}>
                 <h3 className="text-center"> Listings:</h3>
                  </LazyHero>
                  </div>
            <div className="my-2 py-3">

                
                {listings.loading && <center><em>Loading listings....</em></center>}
                {listings.error && <span className="text-danger">ERROR: {listings.error} </span>}
                {listings.items && 
                    <Container>
                    <StackGrid columnWidth={350} gutterHeight={20} gutterWidth={30} monitorImagesLoaded={true}>
                    { listings.items.map((listing, index) =>  
                       <ListingCard key={index} listing={listing} />
                    )}
                </StackGrid>
                </Container>}
                 
            </div>
            </div>
        );
    }
}
class ListingCard extends React.Component {
    constructor(props) {
        super(props);
         this.state = {
             requestInfo: {...INITIAL_STATE },
             submitted: false,
             show: false 

        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
        this.clickOnListing = this.clickOnListing.bind(this);
        this.closeView = this.closeView.bind(this)
        
    }
   
    clickOnListing(event, listing){
        const {address } = listing;
        console.log(listing);
         this.setState({show: true,
                        address: address});
        console.log("Clicked on listing " + address );
        console.log(this.state.show);
    }
    closeView(){
        this.setState({show: false});
        console.log(this.state.show)
    }
    handleChange(event) {
        const { name, value } = event.target;
        const { requestInfo } = this.state;
        this.setState({
            requestInfo: {
                ...requestInfo,
                [name]: value
            }
        });
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: false });
        
        //const { dispatch, user } = this.props;
        //const { _id } = user;
        //requestInfo.id = _id;
        /*if (requestInfo.title && requestInfo.desc ) {
            dispatch(messages.create(requestInfo));
        }*/
        this.setState({ requestInfo: { ...INITIAL_STATE },
                      show: false,
      });
       console.log(this.state);
    }
    handleCancel() {
      this.setState({ requestInfo: { ...INITIAL_STATE },
                      show: false,
      });
      

    }
      

    
    render(){
        const data = this.props.listing.images;
        
        var images = [];
        images = data.map((data, index) => {
            var image = '';
            var bytes = [].slice.call(new Uint8Array(data.data.data));
            bytes.forEach((b) => image += String.fromCharCode(b));
            const baseName = data.baseName;
            return {image: image, baseName: baseName};
        });
        

        const {listing } = this.props;
         const {  requestInfo  } = this.state
        
        
        

        return( 
            <Card  bg="white" text="white" border="dark "style={{ width: '22rem'}}>
                                 <Card.Img className="mx-auto py-0 my-0 mx-0 px-0" variant="top" src={images[0].image} alt={images[0].baseName}/>

                                    <Card.Header className="bg-primary" ><span>{listing.address }</span><br/><span>{listing.city}</span></Card.Header>
                                    <Card.Body className="bg-primary">
                                        
                                        
                                        
                                        <Row className="my-1 py-1 align-items-center">
                                        <Col  className=" text-center">
                                         <FontAwesomeIcon icon={["fas", "bed"]}/> : {listing.bed}</Col>
                                         <Col md ="auto"className=" text-center">
                                            <FontAwesomeIcon icon={["fas", "bath"]}/> : {listing.bath}</Col>
                                            <Col className=" text-center">
                                            <FontAwesomeIcon icon={["fas", "car"]}/> : {listing.parking} </Col>
                                            </Row>
                                            <Row className=" align-items-center">
                                            <Col className=" text-center">
                                            <FontAwesomeIcon icon={["fas", "home"]}/> : {listing.space}</Col>
                                            <div className="col text-center">
                                            <FontAwesomeIcon icon={["fas", "dollar-sign"]}/> : {listing.price}</div>
                                            </Row>
                                            

                                        <Button variant="outline-dark" className="float-right"onClick={ (event) => {this.clickOnListing(event, listing) }}>request info</Button>
                                        <Modal show= {this.state.show}
                                                onHide = {() => {this.closeView() }}
                                              size="lg"
                                              aria-labelledby="contained-modal-title-vcenter"
                                              centered  
                                        >
                                        <Modal.Header closeButton>
                                            <Modal.Title id="contained-modal-title-vcenter">
                                                <h2>Request Infomation on  {this.state.address}</h2>
                                            </Modal.Title>
                                        </Modal.Header>
                                        <Modal.Body>
                                            <div className="container">
                                                <div className="row align-items-center">
                                                    <div className="col-10 offset-1 px-3 py-3">
                                                        <form name="form" onSubmit={this.handleSubmit}>
                                                            <div className={'form-group' + ( !requestInfo.desc ? ' has-error' : '')}>
                                                                <textarea className="form-control"  value={this.state.message} onChange={this.handleChange} rows="10" cols="80"/>
                                                                {!requestInfo.desc && <div className="help-block"> Message is required</div>}
                                                            </div>
                                                            
                                                        </form>
                                                    </div>
                                                </div>
                                            </div>
                                        </Modal.Body>
                                        <Modal.Footer>
                                            <div className="form-group">
                                                <button className="btn btn-primary" onClick={this.handleSubmit}>Requesting Information</button>
                                                
                                                <button className="btn btn-link" onClick={this.handleCancel} >Cancel</button>
                                            </div>
                                        </Modal.Footer>
                                    </Modal>
                                </Card.Body>
                            </Card>

            );
    }

}
function mapStateToProps(state) {
    const { listings } = state;

    return {
        
        listings
        

    };
}

const connectedListingPage = connect(mapStateToProps)(ListingPage);
export { connectedListingPage as ListingPage };



