import React from 'react';
import { connect } from 'react-redux';
import {  testimonialActions } from '../_actions';

const INITIAL_STATE = {
	quote: '',
	person: '',
	error: null,
};

class TestimonialAdd extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			newTestimonial: {
				...INITIAL_STATE
			},
			submitted: false
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleRest = this.handleRest.bind(this);
	}
	handleRest(event) {
		this.setState(
				{
					newTestimonial:
					{ quote: '',
					  person: '' },
					submitted: false
				});
	}
	handleChange(event) {
		const { name, value } = event.target;
		const { newTestimonial } = this.state;
		this.setState({
			newTestimonial: {
				...newTestimonial,
				[name]: value
			}
		});
	}

	handleSubmit(event) {
		event.preventDefault();
		const { newTestimonial } = this.state;
		const { dispatch  } = this.props;
		if (newTestimonial.person && newTestimonial.quote ) {
			dispatch(testimonialActions.create(newTestimonial));
		}
		this.setState({
			newTestimonial:
			{ 
				quote: '',
				person: '' 
			},
			submitted: false
		});
	}

	render() {
		
		const { newTestimonial, submitted } = this.state;
  

		return (
			<div>
				<h2>Testimonial</h2>
				<form name="form" onSubmit={this.handleSubmit} onInput={this.handleChange} onReset={this.handleRest} >
					<div className={'form-group' + (submitted && !newTestimonial.quote ? ' has-error' : '')}>
						<textarea type="text" className="form-control" name="quote" placeholder="Enter Quote" value={newTestimonial.quote} onChange={this.handleChange} />
						{submitted && !newTestimonial.quote && <div className="help-block">Testimonial quote is required</div>}
					</div>
					<div className={'form-group' + (submitted && !newTestimonial.person ? ' has-error' : '')}>
						<input type="text" className="form-control" name="person" placeholder="Who said the quote" value={newTestimonial.person }  onChange={this.handleChange} />
							{submitted && !newTestimonial.title&& <div className="help-block">Person being quoted is required</div>}
					</div>
					<div className="form-group text-center">
						<button className="btn btn-dark">New Testimonial</button>
					</div>
				</form>
			</div>
		);
	}
}
function mapStateToProps(state) {
	const { newTestimonial } = state
	return {
	   newTestimonial
	};
}
const connectedTestimonialAdd = connect(mapStateToProps)(TestimonialAdd);
export { connectedTestimonialAdd as TestimonialAdd };