import React from 'react';

import { connect } from 'react-redux';

import {  listingActions, imagesActions } from '../_actions';


//import FileBase64 from 'react-file-base64';
const INITIAL_STATE = {
    newListing: {
        address: '',
        city: '',
        bath: '',
        bed: '',
        parking: '',
        space: '',
        price: '',
        location:'',
        status: 'HIDDEN',
        
    },
    images: [], 

    
    };
    const fileInput= {
  'borderBottom': '4px solid lightgray',
  'borderRight': '4px solid lightgray',
  'borderTop': '1px solid black',
  'borderLeft': '1px solid black',
  'padding': '10px',
  'margin': '15px',
  'cursor': 'pointer'
  }
 

class ListingAddPage extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            ...INITIAL_STATE,
            
            error: null,
            submitted: false,
            
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleRest = this.handleRest.bind(this);
        
    }
    componentDidMount() {
        this.props.dispatch(imagesActions.getAll());
    }
    handleRest(event) {
        this.setState(
        {
            ...INITIAL_STATE,
            error: null,
            submitted: false
        });
    }
   
  handleDelete(id) {
        const { image } = this.state.newListing;

        this.setState({
            images: [ image.map((image) => {
                if (image.imagePreviewUrl !== id.imagePreviewUrl)
                    return image;
                  
            })]
        })
    }
   _fileAdded(e) {
    e.preventDefault();
    this.setState({
      images: []
    });
    const files = e.target.files;

  setTimeout(() => {
  for (var i = 0; i < files.length; i++) {
      var temp  = files.item(i);
      this.setupReader(temp);
      }
  }, 500);
}
  handleChange(event) {
        const { name, value } = event.target;
        const { newListing } = this.state;
        
        this.setState({
            newListing: {
                ...newListing,
                [name]: value,
                
            }

        });
        
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: true });
        const { newListing, images } = this.state;
        const {  dispatch } = this.props;
        //console.log(newListing);
        const fullListing = {
            ...newListing,
            images: images.map((image) => {
                
                const data = {
                contentType: image.file.type,
                data: image.imagePreviewUrl,
                baseName: image.file.name
                };
                
                return data;
            })
        }
       
        if (newListing.address && newListing.city ) {
            dispatch(listingActions.create(fullListing));
            
        }
        this.setState({
            ...INITIAL_STATE,
            error: null,
            submitted: false,
            defaultKey: Date.now()
        });
        
    }
   resetForm(){
           this.setState({newListing: {}, defaultKey: Date.now() });
           console.log(this.state);
    }
    setupReader(file) {
        
        var reader = new FileReader();  
         reader.onloadend = () => {
           const { images } = this.state;
            console.log(reader.result);
            this.setState({
                images : [...images, {
                file: file,
                imagePreviewUrl:  reader.result
          }]
          
        });
      }
    reader.readAsDataURL(file);
  }
  imageLoad(images){
    var imgCss = {
            'maxWidth' : '140px',
            'maxHeight': '140px',
          };
    let img = [];
    for (var i = 0; i < images.length; i++) {
       img[i] = <div key={i} className="col mx-1 px-1 my-1 py-1"><img src={images[i].imagePreviewUrl} style={imgCss}/></div>;
      }
      return img;
  }

    render() {
        
        const { newListing, submitted, images } = this.state;
        
        
          var canSubmit = false;
          if (!newListing.status && !newListing.bath && !newListing.price  && !newListing.bed && !newListing.tile && !images.images && !newListing.city && !newListing.title) {
              canSubmit = true;
          }

    

    let $imagePreview = null;
    if (images.length > 0) {
      $imagePreview = (this.imageLoad(images));
    } else {
      $imagePreview = ( <div key="0"></div>);
    }
        return (
            <div>
                <h2>New Listing</h2>
                <div className="row my-2 py-2 content-fil-center">
                <div className="container justify-content-center">
        <form  submit="" >
          <input style={fileInput}
            type="file" 
            onChange={(e)=>this._fileAdded(e)} 
            name="myFile"
            key={this.state.defaultKey}
            multiple 
             accept="image/*"/>
          
        </form>
        <div className ="container row">
        
          {$imagePreview}
        
        </div>
      </div>
                
                </div>
                <div className="row my-2 py-2">
                <div className="col">
                <form name="form" onSubmit={this.handleSubmit} onInput={this.handleChange} onReset={this.handleRest} >
                    
                    <div className={'form-group row' + (submitted && !newListing.address ? ' has-error' : '')}>
                        <input type="text" className="form-control" name="address" placeholder="Address" value={newListing.address }  onChange={this.handleChange} />
                            {submitted && !newListing.desc && <div className="help-block">Address for listing Required</div>}
                    </div>
                    <div className={'form-group' + (submitted && !newListing.city ? ' has-error' : '')}>
                        <input type="text" className="form-control" name="city" placeholder="Enter City" value={newListing.city} onChange={this.handleChange} />
                        {submitted && !newListing.city && <div className="help-block">Listing City is required</div>}
                    </div>
                    <div className={'form-group row'}>
                         
    

                            <div className="col">
                                <input type="text" className="form-control" name="bed" placeholder ="Bedrooms" value={newListing.bed} onChange={this.handleChange} />
                                {submitted && !newListing.bed && <div className="help-block">Listing Bed is required</div>}
                            </div>
                            
                            <div className="col">
                                <input type="text" className="form-control" name="bath" placeholder ="Bathrooms" value={newListing.bath} onChange={this.handleChange} />
                            </div>
                        </div>
                         <div className={'form-group row'}>
                            
                            <div className="col">
                                <input type="text" className="form-control" name="parking" placeholder ="Parking" value={newListing.parking} onChange={this.handleChange} />
                                {submitted && !newListing.parking && <div className="help-block">Listing Parking is required</div>}
                            </div>
                            
                            <div className="col"><input type="text" className="form-control" placeholder ="Floor Space" name="space" value={newListing.space} onChange={this.handleChange} /></div>
                        </div>
                        <div className={'form-group row'}>
                            
                            <div className="col">
                                <input type="text" className="form-control" name="location" placeholder ="Location URL" value={newListing.location} onChange={this.handleChange} />
                                {submitted && !newListing.location && <div className="help-block">Listing city is required</div>}
                            </div>
                            
                            <div className="col"><input type="text" className="form-control" placeholder ="Price for unit" name="price" value={newListing.price} onChange={this.handleChange} /></div>
                        </div>
                        <div className={'form-group row ' + (submitted && !newListing.status ? ' has-error' : '')}>
                            
                            <div> <select className="form-control" name="status" value={newListing.status} onChange={this.handleChange} >
                                <option>HIDDEN</option>
                                <option>SHOWING</option>
                                <option>RENTING</option>
                                <option>SOLD</option>
                                <option>OLDER</option>
                            </select></div>
                        </div>
                         <div className={'form-group mx-sm-2 mb-2 row' + (submitted && !newListing.images ? ' has-error' : '')}></div>
                         
                    <div className="form-group text-center">
                        <button type="submit" disabled={canSubmit} className="btn btn-dark">New Listing</button>
                        <button type="rest"  className="btn btn-dark">rest</button>
                    </div>
                </form>
                </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
   
    return {
        
    };
}

const connectedListingAddPage = connect(mapStateToProps)(ListingAddPage);
export { connectedListingAddPage as ListingAddPage };
