import React from 'react';
import { connect } from 'react-redux';
import LazyHero from 'react-lazy-hero';
import house1 from '../Images/house1.jpg';
const INITIAL_STATE = {


    title: '',
    message: 'Enter a Message...',
    error: null,

    
    };
class ListHouse extends React.Component {
  constructor(props) {
        super(props);

        this.state = {
            listHouse: {
                ...INITIAL_STATE
            },
            submitted: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleCancel = this.handleCancel.bind();
    }

    handleChange(event) {
        const { name, value } = event.target;
        const { listHouse } = this.state;
        this.setState({
            listHouse: {
                ...listHouse,
                [name]: value
            }
        });
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: false });
        
        
        
        
        /*if (listHouse.title && listHouse.desc ) {
            dispatch(messages.create(listHouse));
        }*/
    }
    handleCancel() {
      

    }

    render() {
        
        const { listHouse, submitted } = this.state;

        return (
            <div>
             <div>   
                        <LazyHero imageSrc={house1} minHeight={'10vh'} isFixed={true}  parallaxOffset={100}>
                 <h2 className="text-center">List House</h2>
                  </LazyHero>
                  </div>
            <div className="my-2 py-3" >
            <div className="row align-items-center">
            <div className="col-10 offset-1 px-3 py-3">
                
                <form name="form" onSubmit={this.handleSubmit}>
                    <div className={'form-group' + (submitted && !listHouse.title ? ' has-error' : '')}>
                        
                        <input type="text" className="form-control" name="title" value={listHouse.title} onChange={this.handleChange} />
                        {submitted && !listHouse.title&&
                            <div className="help-block"> Title is required</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !listHouse.desc ? ' has-error' : '')}>
                        
                        <textarea className="form-control"  value={this.state.message} onChange={this.handleChange} rows="10" cols="80"/>
                        {submitted && !listHouse.desc &&
                            <div className="help-block"> Message is required</div>
                        }
                    </div>

                    <div className="form-group">
                        <button className="btn btn-primary">Requesting Information</button>
                        { submitted &&
                            <img src="data:image/gif;base64," />
                        }
                        <button className="btn btn-link" onClick={this.handleCancel} >Cancel</button>
                    </div>
                </form>
                </div>
            </div>
            </div>
            </div>
        );
    }
    
}
function mapStateToProps(state) {
    return {      
    };
}

const connectedListHouse = connect(mapStateToProps)(ListHouse);
export { connectedListHouse as ListHouse };

export default ListHouse;





