import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { routesConstants } from '../_constants';
import { ListingAddPage, ListingEdit } from '../Listings';

import { TestimonialAdd, TestimonialEdit } from '../Testimonial';
import { Card, Button, Accordion} from 'react-bootstrap';

class REAPage extends React.Component {
	constructor(props) {
		super(props);

		this.resetForm = this.resetForm.bind(this);
	}
	resetForm(){
			this.setState({newListing: {} });
	}
	render() {

		const rowVal = "row justify-content-center";
		const colVal = "col";
		const mainRow = "col-lg-6 col-xl-6 col-md-12 col-sm-12 col-xs-12 justify-content-center";
		
		return (
			<div className="container">
				<div className="row justify-content-center">
					<div className="col text-center">
						<div className="btn btn-light"><Link  className="text-dark" to={routesConstants.LOGIN}>Logout</Link></div>
					</div>
				</div>
				<div className="row justify-content-center ">
					<div className={mainRow}>
						<div className ={rowVal}>
							<div className ={colVal}>
								<Accordion defaultActiveKey="1">
									<Card className="text-center" bg="primary" text="white">
										<Card.Header>
											<Accordion.Toggle as={Button} variant="dark" eventKey="0" >
											Add Testimonial
											</Accordion.Toggle>
										</Card.Header>
										<Accordion.Collapse eventKey="0">
											<Card.Body>
												<TestimonialAdd {...this.props}/>
											</Card.Body>
										</Accordion.Collapse>
									</Card>
									<Card className="text-center" bg="primary" text="white">
										<Card.Header>
											<Accordion.Toggle as={Button} variant="dark" eventKey="1" >
												Edit Testimonials
											</Accordion.Toggle>
										</Card.Header>
										<Accordion.Collapse eventKey="1">
											<Card.Body>
												<TestimonialEdit />
											</Card.Body>
										</Accordion.Collapse>
									</Card>
								</Accordion>
							</div>
						</div>
					</div>
					<div className={mainRow}>
						<div className ={rowVal}>
							<div className ={colVal}>
								<Accordion defaultActiveKey="1">
									<Card className="text-center" bg="success" text="white">
									<Card.Header>
											<Accordion.Toggle as={Button} variant="dark" eventKey="0" >
												Add Listing
											</Accordion.Toggle>
										</Card.Header>
										<Accordion.Collapse eventKey="0">
											<Card.Body>
												<ListingAddPage {...this.props} />
											</Card.Body>
										</Accordion.Collapse>
										
									</Card>
									<Card className="text-center" bg="success" text="white">
										<Card.Header>
											<Accordion.Toggle as={Button} variant="dark" eventKey="1" >
												Edit Listings
											</Accordion.Toggle>
										</Card.Header>
										<Accordion.Collapse eventKey="1">
											<Card.Body>
												<ListingEdit />
											</Card.Body>
										</Accordion.Collapse>
									</Card>
								</Accordion>
							</div>
						</div>
					</div>
				</div>
			</div>
			
		);
	}
}

function mapStateToProps(state) {
	const { users, authentication,testimonial,listings } = state;
	const { user } = authentication;
	return {
		user,
		users,
		listings,
		testimonial
	};
}

const connectedREAPage = connect(mapStateToProps)(REAPage);
export { connectedREAPage as REAPage };


