import { testimonialConstants } from '../_constants';
import { testimonialService } from '../_services';
import { alertActions } from './';
import { history } from '../_helpers';

export const testimonialActions = {
    getAll,
    getById,
    update,
    create,
    delete: _delete
};


function create(testimonial) {
    return dispatch => {
        dispatch(request(testimonial));

        testimonialService.create(testimonial)
            .then(
                testimonial => { 
                    dispatch(success(testimonial));
                    //history.push('/home');
                    dispatch(alertActions.success('Adding testimonial successful'));
                    //dispatch(getAll());
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(testimonial) { return { type: testimonialConstants.ADD_REQUEST, testimonial } }
    function success(testimonial) { return { type: testimonialConstants.ADD_SUCCESS, testimonial } }
    function failure(error) { return { type: testimonialConstants.ADD_FAILURE, error } }
}

function getAll() {
    return dispatch => {
        dispatch(request());

        testimonialService.getAll().then(
                testimony => dispatch(success(testimony)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: testimonialConstants.GETALL_REQUEST } }
    function success(testimony) { return { type: testimonialConstants.GETALL_SUCCESS, testimony } }
    function failure(error) { return { type: testimonialConstants.GETALL_FAILURE, error } }
}

function getById(id){
    return dispatch => {
        dispatch(request(id));

        testimonialService.getById(id).then(
            testimony => dispatch(success(testimony)),
            error => dispatch(failure(error.toString()))
        );
    };
    function request(id) { return { type: testimonialConstants.GET_REQUEST } } 
    function success(testimony) { return { type: testimonialConstants.GET_SUCCESS, testimony } } 
    function failure(error) { return { type: testimonialConstants.GET_FAILURE } } 
}
function update(id, dataParam){
    return dispatch => {
        dispatch(request(id));

        testimonialService.getById(id).then(
            testimony => dispatch(success(testimony)),
            error => dispatch(failure(error.toString()))
        );
    };
    function request(id) { return { type: testimonialConstants.GET_REQUEST } } 
    function success(testimony) { return { type: testimonialConstants.GET_SUCCESS, testimony } } 
    function failure(error) { return { type: testimonialConstants.GET_FAILURE } } 
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));

        testimonialService.delete(id)
            .then(
                testimonial => {
                dispatch(success(id));
                 },
                error => dispatch(failure(id, error.toString()))
            );
    };

    function request(id) { return { type: testimonialConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: testimonialConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: testimonialConstants.DELETE_FAILURE, id, error } }
}