import React from 'react';
import LazyHero from 'react-lazy-hero';
import { connect } from 'react-redux';
import {  testimonialActions } from '../_actions';
import StackGrid from "react-stack-grid";
import { Card, Container,  Col} from 'react-bootstrap';
import house2 from '../Images/house2.jpg';
class TestimonialList extends React.Component {
    constructor(props) {
        super(props);
        this.state = { testimonial : '' };
        //this.clickOnListing = this.clickOnListing.bind(this);
    }
    componentDidMount() {
        this.props.dispatch(testimonialActions.getAll());
        
    }
    
    clickOnListing(event, listing){
        const {title } = listing;

        console.log("Clicked on listing " + title );
    }
    render() {
        const {  testimonial } = this.props;

        
       
        return (
            <div>
            <div>   
                        <LazyHero imageSrc={house2} minHeight={'10vh'} isFixed={true}  parallaxOffset={100}>
                 <h3 className="text-center">Testimonials</h3>
                  </LazyHero>
                  </div>
            <div className="my-2 py-3" >

                
                {testimonial.loading && <em>Loading testimonials....</em>}
                {testimonial.error && <span className="text-danger">ERROR: {testimonial.error} </span>}
                {testimonial.items &&  
                    <Container className="justify-items-center">
                    <StackGrid columnWidth={350} gutterHeight={5} gutterWidth={5}>
                    
                   
                    { testimonial.items.map((testimony, index) => 
                        <Col key={testimony.id} className="text-center" >
                        <Card bg="light" text="dark"border="dark" className="p-0 m-0 align-items-middle" style={{ width: '21rem' }}>
                            <blockquote className="blockquote mb-0 card-body">
                                <p>{testimony.quote}</p>
                                <footer className="blockquote-footer text-right">
                                    <small className="text-muted">
                                        <cite title={testimony.person}>{testimony.person}</cite>
                                    </small>
                                </footer>
                            </blockquote>
                        </Card>
                                 
                        </Col>
                    )}
                    
                    
                </StackGrid>
                </Container>
                }
            </div>
            </div>
        );
    }
}

function mapStateToProps(state) {

const { testimonial } = state;

    return {
        
        testimonial
        

    };
}

const connectedTestimonialList = connect(mapStateToProps)(TestimonialList);
export { connectedTestimonialList as TestimonialList };