import { listingConstants } from '../_constants';
let storedListing = JSON.parse(localStorage.getItem('listings'));

const initState = storedListing ? { gotList: true, loading: false, submitted: false, items: [...storedListing] } : {} ;
export function listings(state = initState, action) {
  switch (action.type) {
     case listingConstants.ADD_REQUEST:
       return { ...state, submitted: true,  
                };
     case listingConstants.ADD_SUCCESS:

       return { loading: false,
                gotList: true, 
                submitted: false,
                items: action.items.listings,
                 };
    case listingConstants.ADD_FAILURE:
      return { submitted: false,
               items: [...listings],
               error: action.error };
    case listingConstants.GETALL_REQUEST:
      return {
        loading: true
      };
    case listingConstants.GETALL_SUCCESS:
      return {
        items: action.listings,
        loading: false
      };
    case listingConstants.GETALL_FAILURE:
      return { 
        error: action.error,
        loading: false
      };
    case listingConstants.DELETE_REQUEST:
      // add 'deleting:true' property to listing being deleted
      return {
        ...state,
        items: state.items.map(listing =>
          listing.id === action.id
            ? { ...listing, deleting: true }
            : listing
        )
      };
    case listingConstants.DELETE_SUCCESS:
      // remove deleted listing from state
      return {
        items: state.items.filter(listing => listing.id !== action.id)
      };
    case listingConstants.DELETE_FAILURE:
      // remove 'deleting:true' property and add 'deleteError:[error]' property to listing 
      return {
        ...state,
        items: state.items.map(listing => {
          if (listing.id === action.id) {
            // make copy of listing without 'deleting:true' property
            const { deleting, ...listingCopy } = listing;
            // return copy of listing with 'deleteError:[error]' property
            return { ...listingCopy, deleteError: action.error };
          }

          return listing;
        })
      };
    default:
      return state
  }
}