import { imagesService } from '../_services';
import { alertActions } from './';
import { history } from '../_helpers';
import { imagesConstants } from '../_constants';


export const imagesActions = {
    getAll,
    getById,
    create,
    update,
    delete: _delete
};


function create(image) {
    return dispatch => {
        dispatch(request(image));

        imagesService.create(image)
            .then(
                image => { 
                    console.log("Uploaded Image");
                    console.log(image);
                    
                    

                    //history.push('/home');
                    dispatch(alertActions.success('Upload Image successful'));
                    //dispatch(getAll());
                    dispatch(success(image));
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(image) { return { type: imagesConstants.ADD_REQUEST, image } }
    function success(image) { return { type: imagesConstants.ADD_SUCCESS, image } }
    function failure(error) { return { type: imagesConstants.ADD_FAILURE, error } }
}

function getAll() {
    return dispatch => {
        dispatch(request());

        imagesService.getAll().then(
                images => dispatch(success(images)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: imagesConstants.GETALL_REQUEST } }
    function success(images) { return { type: imagesConstants.GETALL_SUCCESS, images } }
    function failure(error) { return { type: imagesConstants.GETALL_FAILURE, error } }
}

function getById(id){
    return dispatch => {
        dispatch(request(id));

        imagesService.getById(id).then(
            listing => dispatch(success(listing)),
            error => dispatch(failure(error.toString()))
        );
    };
    function request(id) { return { type: imagesConstants.GET_REQUEST, id  }} 
    function success(listing) { return { type: imagesConstants.GET_SUCCESS, listing } } 
    function failure(error) { return { type: imagesConstants.GET_FAILURE } } 
}
function update(id){
    return dispatch => {
        dispatch(request(id));

        imagesService.update(id)
            .then(
                listing => dispatch(success(id)),
                   error => dispatch(failure(id, error.toString()))
                );

    };
    function request(id) { return { type: imagesConstants.UPDATE_REQUEST, id } } 
    function success(id) { return { type: imagesConstants.UPDATE_SUCCESS, id } } 
    function failure(id, error) { return { type: imagesConstants.UPDATE_FAILURE, id, error } } 
}
// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));

        imagesService.delete(id)
            .then(
                id => dispatch(success(id)),
                error => dispatch(failure(id, error.toString()))
            );
    };

    function request(id) { return { type: imagesConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: imagesConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: imagesConstants.DELETE_FAILURE, id, error } }
}