import { messageConstants } from '../_constants';

export function messages(state = {}, action) {
  switch (action.type) {
    case messageConstants.GETALL_REQUEST:
      return {
        loading: true
      };
    case messageConstants.GETALL_SUCCESS:
      return {
        loading: false,
        items: action.messages
      };
    case messageConstants.GETALL_FAILURE:
      return { 
        error: action.error
      };

    case messageConstants.DELETE_REQUEST:
      // add 'deleting:true' property to message being deleted
      return {
        ...state,
        items: state.items.map(message =>
          message.id === action.id
            ? { ...message, deleting: true }
            : message
        )
      };
    case messageConstants.DELETE_SUCCESS:
      // remove deleted message from state
      return {
        items: state.items.filter(message => message.id !== action.id)
      };
    case messageConstants.DELETE_FAILURE:
      // remove 'deleting:true' property and add 'deleteError:[error]' property to message 
      return {
        ...state,
        items: state.items.map(message => {
          if (message.id === action.id) {
            // make copy of message without 'deleting:true' property
            const { deleting, ...messageCopy } = message;
            // return copy of message with 'deleteError:[error]' property
            return { ...messageCopy, deleteError: action.error };
          }

          return message;
        })
      };
    case messageConstants.NEW_REQUEST:
      return { loading: true };
    case messageConstants.NEW_SUCCESS:
      return { successs: true};
    case messageConstants.NEW_FAILURE:
      return { success: false};
    default:
      return state
  }

}