import { configConst } from '../_constants';
import { authHeader } from '../_helpers';

export const listingService = {
    create,
    getAll,
    getById,
    update,
    delete: _delete
};


function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/listings/index`, requestOptions).then(handleResponse).then(listings => {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            localStorage.setItem('listings', JSON.stringify(listings));

            return listings;
        });
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/listings/listing/${id}`, requestOptions).then(handleResponse);
}

function create(listing) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(listing)
    };

    return fetch(`${configConst.apiUrl}/listings/create`, requestOptions).then(handleResponse);
}

function update(listing) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(listing)
    };

    return fetch(`${configConst.apiUrl}/listings/update/${listing.id}`, requestOptions).then(handleResponse);;
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${configConst.apiUrl}/listings/delete/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                //location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}