import React from 'react';
import { connect } from 'react-redux';
import {  imagesActions } from '../_actions';



 var fileInput= {
  'borderBottom': '4px solid lightgray',
  'borderRight': '4px solid lightgray',
  'borderTop': '1px solid black',
  'borderLeft': '1px solid black',
  'padding': '10px',
  'margin': '15px',
  'cursor': 'pointer'
  }

  var imgCss = {
    'maxWidth' : '140px',
    'maxHeight': '140px',
    
 
  }

var submitButton ={
  'padding': '12px',
  'marginLeft' : '10px',
  'background': 'white',
  'border': '4px solid lightgray',
  'borderRadius': '15px',
  'fontWeight': '700',
  'fontSize': '10pt',
  
}


class ImageUpload extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      images: '' };

  }
 componentDidMount() {
        this.props.dispatch(imagesActions.getAll());
    }
  _handleSubmit(e) {
    e.preventDefault();
    const body = new FormData(this.form);
    
    
    console.log('handle uploading-', body);
    this.props.dispatch(imagesActions.create(body));
    this.setState({
      images: '',
      defaultKey: Date.now()
    });
  }

  _handleImageChange(e) {
    e.preventDefault();
    this.setState({
      images: []
    });
    const files = e.target.files;

  setTimeout(() => {
  for (var i = 0; i < files.length; i++) {
      var temp  = files.item(i);
      this.setupReader(temp);
      }
  }, 500);
    
    
      
    
  }
   setupReader(file) {
    
    var reader = new FileReader();  
     reader.onloadend = () => {
       const { images } = this.state;
        this.setState({
          
          images : [...images, {
            file: file,
            imagePreviewUrl:  reader.result
          }]
          
        }, () => console.log(this.state.new));
      }
    reader.readAsDataURL(file);
  }
  imageLoad(images){
    let img = [];
    for (var i = 0; i < images.length; i++) {
       img[i] = <div key={i} className="col mx-1 px-1 my-1 py-1"><img src={images[i].imagePreviewUrl} style={imgCss}/></div>;
      }
      return img;
  }
  render() {
    let {images} = this.state;

    let $imagePreview = null;
    if (images.length > 0) {
      $imagePreview = (this.imageLoad(images));
    } else {
      $imagePreview = ( <div key="0"></div>);
    }

    return (
      <div className="container justify-content-center">
        <form ref={el => (this.form = el)} onSubmit={(e)=>this._handleSubmit(e)}  >
          <input style={fileInput}
            type="file" 
            onChange={(e)=>this._handleImageChange(e)} 
            name="myFile"
            key={this.state.defaultKey}
            multiple 
             accept="image/*"/>
          <button style={submitButton}
            type="submit" 
            onClick={(e)=>this._handleSubmit(e)}>Upload Image</button>
        </form>
        <div className ="container row">
        
          {$imagePreview}
        
        </div>
      </div>
    )
  }
}
function mapStateToProps(state) {
    
    return {
    
    };
}

const connectedImageUpload = connect(mapStateToProps)(ImageUpload);
export { connectedImageUpload as ImageUpload };


